import React, { Component } from "react";
import lotus_ladies_designerlogo from "../img/logo/lotus_ladies_designerlogo.png";

class Header extends Component {
  state = {
    icon: "fas fa-bars",
    count: 0,
    width: 992,
  };
  check = () => {
    if (this.state.count === 0) {
      this.setState({ icon: "fas fa-times" });
      this.setState({ count: 1 });
    } else {
      this.setState({ icon: "fas fa-bars" });
      this.setState({ count: 0 });
    }
  };

  render() {
    return (
      <nav
        style={{ backgroundColor: "white" }}
        className="navbar navbar-expand-lg navbar-light bg-light"
      >
        <a className="navbar-brand" href="/">
          <img
            className="nav-brand"
            src={lotus_ladies_designerlogo}
            alt="Brand_logo"
          />
        </a>
        {this.print}
        <span
          className="hamburger"
          onClick={this.check}
          data-toggle="collapse"
          data-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i className={this.state.icon}></i>
        </span>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav float-right-nav-ul">
            <li className="nav-item">
              <a className="nav-link" href="#HOME">
                HOME
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#ABOUT_US">
                ABOUT US
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#SERVICES">
                SERVICES
              </a>
            </li>
            <li className="nav-item">
              <a className="nav-link " href="#CONTACT">
                CONTACT
              </a>
            </li>
          </ul>
        </div>
      </nav>
    );
  }
}

export default Header;
